/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block; }

ol,
ul {
  list-style: none; }

.text-center {
  text-align: center; }

.text-right {
  text-align: right; }

.text-left {
  text-align: left; }

.canvas {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: -1; }

.canvas_container {
  width: 60vw;
  height: 60vh; }

html {
  font-size: 18px;
  scroll-behavior: smooth; }

body {
  font-family: interstate, sans-serif;
  font-weight: 400;
  font-style: normal;
  color: #ffffff;
  background-color: #000000; }
  body .background-color {
    height: 1200vh; }
    body .background-color .background-svg {
      width: 100%;
      height: 100%;
      display: flex;
      justify-content: center; }
      body .background-color .background-svg svg#blood-line-svg {
        width: auto;
        height: 100%;
        fill: none;
        stroke: #7B0000;
        stroke-miterlimit: 10;
        stroke-width: 50px; }
      body .background-color .background-svg svg .blood-line {
        transition: all 1s cubic-bezier(0, 0, 0, 1);
        stroke-dashoffset: 16625.4;
        stroke-dasharray: 16625.4;
        stroke-linecap: round; }
      body .background-color .background-svg .fluid-container {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%; }
        body .background-color .background-svg .fluid-container .container {
          max-width: 60vw;
          margin: 0 auto; }
  body .heartbeat-line-svg {
    position: absolute;
    top: 0;
    width: 100%;
    height: 100%;
    z-index: -2; }
    body .heartbeat-line-svg .heartbeat-path {
      stroke: #7B0000;
      stroke-width: 2px;
      stroke-dasharray: 1260;
      stroke-dashoffset: 1260;
      fill: none;
      animation: heartbeat 1.6s linear infinite;
      height: 20px; }

@keyframes heartbeat {
  0% {
    opacity: 0;
    stroke-width: 0px; }
  50% {
    opacity: 1;
    stroke: #7B0000;
    stroke-width: 3px; }
  100% {
    opacity: 0;
    stroke-width: .6px;
    stroke-dashoffset: 2520; } }
  body svg {
    stroke: #000000;
    stroke-width: 5px; }
  body .heartPfadRechts {
    fill: #7B0000; }
  body .heartPfadLinksOben {
    fill: #7B0000; }
  body .heartPfadLinksUnten {
    fill: #7B0000; }

section {
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: row; }

.flex-item {
  padding: 0 15px; }

.col-6 {
  flex: 0 0 50%;
  max-width: 50%; }

.col-4 {
  flex: 0 0 25%;
  max-width: 25%; }

.col-8 {
  flex: 0 0 75%;
  max-width: 75%; }

.section1,
.section2,
.section3,
.section4 {
  position: sticky;
  top: 0;
  transition: all .1s ease; }

.fade-out-element {
  transition: all .1s ease; }

.section2 {
  position: sticky;
  top: 0; }

h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: 700; }

h1 {
  font-size: 5rem; }

@keyframes herzschlag {
  0% {
    transform: scale(0.9); }
  20% {
    transform: scale(1); }
  40% {
    transform: scale(0.9); }
  60% {
    transform: scale(1); }
  80% {
    transform: scale(0.9); }
  100% {
    transform: scale(0.9); } }

.herzschlag {
  animation: herzschlag 1.6s .2s ease-in-out infinite; }
